import { toNumber } from "shared/helpers/number";
import { T_ChitInput, T_Deposit, T_ChitStatus } from "./types";

export function calcChitStatus(chitInput: T_ChitInput): T_ChitStatus {
  const duration = toNumber(chitInput.duration);
  const monthlyPaymentAmount = toNumber(chitInput.monthlyPaymentAmount);
  const noOfPaymentsSoFar = toNumber(chitInput.noOfPaymentsSoFar);
  const agencyCommissionPercent = toNumber(chitInput.agencyCommissionPercent);
  const lastPaymentAmount = toNumber(chitInput.lastPaymentAmount);

  let chitTotal = 0;
  if (duration > 0) {
    chitTotal = monthlyPaymentAmount * duration;
  }
  const noOfMonthsRemaining = duration - noOfPaymentsSoFar;
  const agencyCommissionAmount = (chitTotal * agencyCommissionPercent) / 100;
  const normalChitTotalInHand = chitTotal - agencyCommissionAmount;

  let lastAuctionAmount = 0;
  if (lastPaymentAmount > 0) {
    lastAuctionAmount =
      agencyCommissionAmount +
      (monthlyPaymentAmount - lastPaymentAmount) * duration;
  }

  return {
    chitTotal,
    normalChitTotalInHand,
    noOfMonthsRemaining,
    lastAuctionAmount,
    agencyCommissionAmount
  };
}

export function calcDepositInterestTotal(
  deposits: T_Deposit[],
  duration: number
) {
  return deposits.reduce((total, deposit) => {
    const depositAmount = toNumber(deposit.depositAmount);
    const depositInterestPercent = toNumber(deposit.depositInterestPercent);
    const interestAmount =
      ((depositAmount * depositInterestPercent) / 100 / 12) * duration;

    return total + interestAmount;
  }, 0);
}

export function calcChitAuctionResult(
  chitInput: T_ChitInput,
  { chitTotal, normalChitTotalInHand, agencyCommissionAmount }: T_ChitStatus,
  depositInterestTotal: number
) {
  const yourAuctionAmount = toNumber(chitInput.yourAuctionAmount);
  const auctionedChitTotalInHand = chitTotal - yourAuctionAmount;
  const totalSavingsByAuction = auctionedChitTotalInHand + depositInterestTotal;
  const diffAmountWithNormalChitTotalInHand =
    totalSavingsByAuction - normalChitTotalInHand;

  return {
    auctionedChitTotalInHand,
    totalSavingsByAuction,
    diffAmountWithNormalChitTotalInHand
  };
}
